import React from 'react';
import {Route, Switch} from 'react-router-dom';
import asyncComponent from '../../../../../util/asyncComponent';

const Customers = ({match}) => (
  <Switch>
      <Route exact from={`${match.url}/`} component={asyncComponent(() => import('./CompanyView.js'))}/>
      <Route exact from={`${match.url}/Info/`} component={asyncComponent(() => import('./CompanyInfo.js'))}/>
      <Route exact from={`${match.url}/customer-edit/`} component={asyncComponent(() => import('./CustomerModify.js'))}/>
      <Route component={asyncComponent(() => import('app/routes/extraPages/routes/404'))}/>
  </Switch>
);

export default Customers;